@import '../../constants/variable';

.c_login
{
    background-color: #000;
    padding: $div-gap;
    
    ul
    {
        list-style: none;
        margin: 0;
        display: flex;
        border-bottom: dotted 1px #4a4a4a;
        padding: 0;
        padding-bottom: $div-gap;

        .button
        {
            @include button;

            @include rwd(ipad)
            {
                &:nth-child(2)
                {
                    display: none;
                }
            }
        }

        .unique
        {
            color: $font-purple;
            font-size: 12px;
            display: flex;
            align-items: center;
            margin-left: auto;
            cursor: pointer;
            position: relative;

            i
            {
                margin-left: 8px;
                font-size: 16px;
            }

            .c_prompt
            {
                margin-left: 6px;
            }
        }
    }
    .qrcode
    {
        padding: 10px;
        background-color: #fff;
    }

    &.cover::before
    {
        position: fixed;
        content: '';
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, .7);
        transition: .5s;
    }

}

.swal-overlay
{
    background-color: transparent;
}

.swal-modal
{   
    border: solid 3px rgba(0, 0, 0, .5);

