@import '../../constants/variable';

.co_mortgage
{
    background-color: $bg-black;
    min-height: 100vh;
    position: relative;

    @include rwd(phone)
    {
       height: 100%;
    }

    nav
    {
        @include nav;
    }

    .title
    {
        @include title($bg-right-title);

        &.order
        {
            display: flex;
            justify-content: space-between;

            span
            {
                display: flex;
                
                &:last-child
                {
                    color: $font-purple;
                }
                // cursor: pointer;
            }

            a
            {
                color: $font-purple;
                cursor: pointer;
                text-decoration: none;
            }

            .offset
            {
                display: flex;
            }
        }

        .c_prompt
        {
            margin-left: 8px;
        }
    }

    main
    {
        display: flex;
        padding: 0 $div-gap;
        padding-bottom: 64px;

        @include rwd(ipad)
        {
            flex-direction: column;
            padding: 0 10px;
            margin-bottom: 60px;
        }

        > div
        {
            min-height: 360px;
            background-color: $bg-right;
            
            &:first-child
            {
                flex: .5;
                margin-right: $div-gap;
                box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);

                @include rwd(ipad)
                {
                    flex: 1;
                    margin-right: 0;
                    order: 2;
                }
            }

            &:last-child
            {
                flex: 1;

                @include rwd(ipad)
                {
                    margin-bottom: 16px;
                    height: auto;
                }
            }
        }

        .c_login
        {
            background-color: transparent;
        }
    }

}
