@import '../../constants/variable';

.c_mortgageUser
{
    color: $font-gray;
    
    .logout
    {
        position: relative;
        padding: $div-gap;
        display: flex;
        justify-content: flex-end;

        &::after
        {
            position: absolute;
            content: '';
            height: 1px;
            width: calc(100% - 32px);
            left: 16px;
            bottom: 0;
            border-bottom: 1px dotted #4a4a4a;
        }
    }
    
    .button
    {
        @include button;
        width: 80px;
    }

    h3
    {
        color: $font-gray;
        font-size: 14px;
        margin: $div-gap;
    }

    ul
    {
        list-style: none;
        color: #fff;
        padding: 0 $div-gap;
        font-size: 14px;

        li
        {
            display: flex;
            margin-bottom: 18px;

            > div
            {
                &:first-child
                {
                    width: 64px;
                }

                &:last-child
                {
                    flex: 1;
                }
            }
        }
    }
}