@import '../../constants/variable';

.c_mortgageOrder
{
    display: flex;
    flex-direction: column;
    color: $font-gray;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);

    @include rwd(ipad)
    {
        height: auto;
    }

    .order-info
    {
        padding: 24px;
        display: flex;
        font-size: 14px;
        color: #fff;
        position: relative;
        flex-direction: column;

        @include rwd(ipad)
        {
            flex-direction: column;
        }

        .mortgage-rate
        {
            display: flex;
            padding-bottom: 24px;

            @include rwd(ipad)
            {
                flex-direction: column;
            }

            p
            {
                margin: 0;
            }

            .infor
            {
                display: flex;
                flex-direction: column;
                flex: 1;
                
                .rate
                {
                    display: flex;

                    span
                    {
                        &:last-child
                        {
                            display: flex;
                            align-items: center;
                            position: relative;
                        }
                    }
                }

                > div
                {
                    display: flex;
                    margin-bottom: 9px;
                    color: $font-gray;

                    &:first-child
                    {
                        color: #fff;
                    }
                }
            }

            .close-line
            {
                flex: 1;

                .rate
                {
                    display: none;
                }
            }

            > div
            {
                > div 
                {
                    display: flex;

                    span
                    {
                        flex: 1;
                        display: flex;

                        &[data-tip='React-tooltip']
                        {
                            cursor: pointer;
                        }
                    }
                }
            }
        }

        .c_prompt
        {
            margin-left: $div-gap;
        }

        .mortgage-amount
        {

            .table
            {
                padding: 24px 0;
                
                > div
                {
                    display: grid;
                    grid-template-columns: 10% repeat(3, 33.333%);
                    // grid-template-columns: 10% repeat(4, 22.5%);
                    margin-bottom: $div-gap;
                    grid-gap: 3%;

                    span
                    {
                        width: 50%;
                        cursor: pointer;

                        &:nth-child(even)
                        {
                            text-align: left;
                        }
                        
                        &:nth-child(odd)
                        {
                            text-align: left;
                        }
                    }

                    > div
                    {
                        color: $font-purple;
                    }
                }

                .t-header
                {
                    color: $font-gray;
            
                    &:nth-child(odd)
                    {
                        text-align: left;
                    }
    
                    &:nth-child(even)
                    {
                        // text-align: right;
                        text-align: left;
                    }
                }
            }
        }
    }

    .mortgage-input
    {
        padding: 24px;
        display: flex;
        flex-direction: column;
        font-size: 14px;

        &:first-child
        {
            border-top: none;
            
            .item
            {
                margin-bottom: 0;

                .input-field
                {
                    max-width: initial;
                    min-width: initial;
                    position: relative;

                    @include rwd(ipad)
                    {
                        max-width: initial;
                        min-width: initial;
                    }
    
                    input
                    {
                        flex: 4;
                    }

                    span
                    {
                        @include rwd(ipad)
                        {
                            max-width: initial;
                            min-width: initial;
                            width: auto;
                            padding: 0;
                        }
                    }
                }

                .button
                {
                    @include rwd(ipad)
                    {
                        width: initial;
                        margin: 0;
                    }
                }
            }
        }

        .item
        {
            display: grid;
            grid-template-columns: 2fr 5fr 3fr;
            grid-gap: 2%;
            margin-bottom: 24px;

            @include rwd(ipad)
            {
                grid-template-columns: 1fr 1fr;
                grid-gap: 4%;

                > div:first-child
                {
                    display: none;
                }

                > div
                {
                    flex: 1;
                }
            }

            &.selectable
            {
                grid-template-columns: 2fr 1fr 3fr 2fr 3fr;
                grid-gap : 2%;

                @include rwd(desktop)
                {
                    .button
                    {
                        margin: 0;
                        width: auto;
                    }
                }

                @include rwd(ipad)
                {
                    grid-gap: 3%;
                    grid-template-columns: 1fr 3fr 2fr 1fr;
                    div
                    {
                        margin: 0;

                        &:last-child
                        {
                            width: initial;
                        }
                    }
                }

                @include rwd(phone)
                {
                    .input-field
                    {
                        box-sizing: border-box;
                        width: 100%;

                        min-width: initial;
                        max-width: initial;

                        input
                        {
                            box-sizing: border-box;
                            width: 100%;
                        }
                    }
                }
            }

            > div
            {
                display: flex;
                align-items: center;
            }

            .input-field
            {
                max-width: 232px;
                min-width: 152px;
                border: 1px solid $font-gray;
                border-radius: 2px;
                padding: 8px;
                cursor: pointer;

                &.select
                {
                    border: 1px solid #fff;
                }
                
                input
                {
                    background-color: transparent;
                    color: $font-purple;
                    border: none;
                    outline: none;
                    -webkit-text-fill-color: $font-gray;
                    width: 100%;
                    cursor: pointer;

                    &:active, &:focus
                    {
                        -webkit-text-fill-color: #fff;
                    }
                }

                span
                {
                    flex: 1;
                    width: 50%;
                    padding-left: $div-gap;
                }
            }

            .toggle-field
            {
                width: 54px;
                display: flex;
                justify-content: flex-end;
                padding: 0 4px;
                position: relative;
                border: 1px solid $font-gray;
                cursor: pointer;

                img
                {
                    margin-left: 4px;
                }

                .toggle
                {
                    @include toggle(4);
                    top: 36px;
                    text-align: center;
                    height: 100px;
                    overflow: auto;
                    padding-top: 0;

                    &:last-child
                    {
                        height: 85px;
                    }

                    &.close
                    {
                        overflow: hidden;
                        height: 0;
                    }
                }
            }
        }

    }

    .offset-input
    {
        display: flex;
        padding: 24px;
        padding-bottom: 0;

        .item
        {
            margin-right: 30px;
        }
    }

    .button
    {
        @include button;
        width: 120px;
        display: block;
        justify-content: center;

        &.disable
        {
            background-color: #4a4a4a;
            color: $font-gray;
            border-color: #4a4a4a;
            pointer-events: none; 
        }

        &.all
        {
            width: auto;
            margin-left: 0;
            max-width: 120px;
        }
    }

    .divid
    {
        position: relative;
        &::after
        {
            position: absolute;
            content: '';
            height: 1px;
            border-bottom: 1px #4a4a4a dotted;
            bottom: 0;
            width: calc(100% - 32px);
            left: 16px;
        }
       
    }
    

    &.offset
    {
        .order-info .mortgage-rate
        {
            .close-line
            {
                order: 1;
                
                .rate
                {
                    display: flex;
                    margin-top: 9px;
                }
            }

            .infor
            {
                order: 2;
                
                .rate
                {
                    display: none;
                }
            }
        }
        
        .offset-btn
        {
            display: flex;
            justify-content: space-around;
            padding-bottom: 24px;

            @include rwd(phone)
            {
                flex-wrap: wrap;

                > .item
                {
                    margin-bottom: 10px;
                }
            }
        }
        
        .mortgage-rate, .table
        {
            &.divid::after
            {
                display: none;
            }
        }
        
    }

    .__react_component_tooltip.show
    {
        color: #fff !important;
        opacity: 1 !important;
    }
}